
h1{
    font-size:2.25rem;
    font-weight: 700;

}

h2{
    font-size:1.5rem;
    font-weight: 700;
    text-align:center;
}

a{
    text-decoration:none;
    color:var(--primary-blue);
}

a:hover{
    text-decoration:underline;
}

.small{
    font-size:80%;
    text-align:center;
}

.split-screen{
    display:flex;
    flex-direction:column;
}

.left{
    height:200px;

}

.left,.right{
    display:flex;
    justify-content:center;
    align-items:center;
}

.left{
    background:url('./image.jpg') center/35rem 40rem no-repeat;
}

.left.copy{
    color:white; 
    text-align:center;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale;
    
    
}

.copy{
    max-width: 45rem;
}

.left-details{
    transform: translateY(100%);
    margin-top: 10rem;
}

.left p{
    font-weight:400;
    text-align: center;
    max-width: 30rem;
}


.left h1{
    text-align: center;
}

.right.copy{
    color:black;
    text-align:center;
}


.right .copy p{
    margin:1.5em 0;
    font-size:0.875rem; 
}

.right form {
    width: 500px;
}

form input[type="text"],
form input[type="email"],
form input[type="password"]{
    display:block;
    width:100%;
    box-sizing:border-box;
    border-radius:8px;
    border:1px solid #c4c4c4;
    padding:1em;
    margin-bottom:1.25rem;
    font-size:0.875rem;
}

label{
    display:block;
    margin-bottom:0.5rem;
    font-size:0.75rem;
}

.input-container.password{
    position:relative;

}

.input-container.password i{
    position:absolute;
    top:42px;
    right:16px;
}

.checkbox-container input{
    position:absolute;
    opacity:0;
    cursor:pointer;
    height:0;
    width:0;
}

.checkbox-container{
    display:block;
    position:relative;
    padding-left:2rem;
    cursor:pointer;
    user-select:none;
    margin:0 0 1.25rem 0;
}

.checkbox-container input:checked~.checkmark{
    background-color:var(--primary-blue);
    border:1px solid var(--primary-blue);



}

.checkmark{
    position:absolute;
    width:24px;
    height:24px;
    border:1px solid lightskyblue;
    border-radius:2px;
    top:-4px;
    left:0;
}

.error{
    color:red;
    margin-top: 2px;
}

.checkmark:after{
    content:"";
    position:absolute;
    display:none;
}

.checkbox-container input:checked~.checkmark:after{
    display:block;



    

}


.checkbox-container .checkmark:after{
    border:solid blue;
    border-width:0 3px 3px 0;
    width:5px;
    height:10px;
    left:8px;
    top:4px;
    transform:rotate(45deg);

}

.signup-btn{
    display:block;
    width:100%;
    background:lightskyblue;
    color:white;
    font-weight:700px;
    border:none;
    padding:1rem;
    border-radius:8px; 
    font-size:0.75rem;
    text-transform:uppercase;
    letter-spacing:0.5px;
}

.signup-btn:hover{
    background:lightgreen;
    cursor:pointer;
}

@media screen and (min-width:900px){
    .split-screen{
        flex-direction:row;
        height:100vh;
        gap:2rem;

    }

    .left,
    .right{
        display:flex;
        width:50%;
        height:auto;
    }
    
}

@media screen and (min-width:652px){
    .split-screen{
        flex-direction:row;
        height:100vh;
        gap:2rem;

    }

    
    .left,
    .right{
        display:flex;
        width:50%;
        height:auto;
        flex:1;
    }

    .left-details{
        /* display:none; */
        overflow: hidden;
        transform: translateY(50%);
        font-size: 1em;
    }
    
}

@media screen and (max-width:690px){
    .split-screen{
        flex-direction:row;
        height:100vh;

    }

    
    .right{
        display:flex;
        width:50%;
        height:auto;
        margin:auto 8rem;
    }

    .left-details{
        display: none;
    }

    

    

    
